import React, { Component } from 'react';

class InputAmount extends Component {
    constructor(props) {
        super(props);
        this.selectText = this.selectText.bind(this);
    }

    selectText() {
    	const input = document.getElementById(this.props.label);
    	input.focus();
    	input.select();
    }

    render() {
        return (
        	<span className="inputs">
        		<label for={this.props.label}><i className={this.props.icon}></i></label>
        		<input id={this.props.label} type="text" onChange={this.props.change} value={this.props.value} onClick={this.selectText}/>
        	</span>
        );
    }
}

export default InputAmount;
