import React, { Component } from 'react';

class AdditionalInfo extends Component {
    constructor(props) {
        super(props);

    }

    render() {
    
        return (

        	<div id="additional-info">
        		<p>Last year's rate: <span className="text-bold">{this.props.hist_rate}</span>
        		<br />
        		That's a <span className="text-bold">{this.props.riseFallPercentage}%</span> change over a year.</p>

        	</div>
            
        );
    }
}

export default AdditionalInfo;
