import React, { Component } from 'react';
import InputAmount from './components/input-amount';
import AdditionalInfo from './components/additional-info';
import GetRateButton from './components/get-rate-btn';
import ShareBar from './components/share-bar';
import Tooltip from './components/tooltip';
import CurrentRateData from './components/current-rate-data';

class App extends Component {
  constructor(props){
    super(props)
    this.state = { gbp: 0, eur: 0, hideLatest: true }
    this.getRate();
    this.handleEvent = this.handleEvent.bind(this);
    this.getRate = this.getRate.bind(this);
  }

  getRate(){
    const days = ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'];
    const months = ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September',
                  'October', 'November', 'December'];
    var xmlr = new XMLHttpRequest();
    xmlr.open('GET', 'https://api.openrates.io/latest?base=GBP', true)
    xmlr.onreadystatechange = ()=>{
      if(xmlr.status == 200 && xmlr.readyState == 4){
        var myJson = JSON.parse(xmlr.responseText)
        if(myJson.rates.EUR == this.state.rate){
          this.setState({ latest: "This is the latest rate available.", hideLatest: false});
          setTimeout(() =>{ this.setState({ latest: "", hideLatest: true}) }, 2000);
        }
        else{
          var date = new Date(myJson.date)
          this.setState({ rate: myJson.rates.EUR, sys: date, date: `${days[date.getDay()]} ${date.getDate()} ${months[date.getMonth()]} ${date.getFullYear()}` });
          var historicXmlr = new XMLHttpRequest();
          historicXmlr.open('GET', `https://api.openrates.io/${this.state.sys.getFullYear() -1}-${this.state.sys.getMonth() + 1}-${this.state.sys.getDate()}?base=GBP`, true)
          historicXmlr.onreadystatechange = ()=>{
            if(historicXmlr.status == 200 && historicXmlr.readyState == 4){
              var myJson = JSON.parse(historicXmlr.responseText)
              var rate = ((this.state.rate - myJson.rates.EUR) / (this.state.rate / 100)).toFixed(2)
              rate = rate > 0 ? `+${rate}` : rate;
              this.setState({ hist_rate: myJson.rates.EUR, hist_perc: rate })
            }
          }
          historicXmlr.send();
        }
      }
    }
    xmlr.send();
    setTimeout(()=>{
    document.getElementById('button-getrate').blur();
  },3000)
  }

  handleEvent(e){
    var regex = /^\d*\.*\d{0,2}$/;
    if(regex.test(e.target.value)){
      if(e.target.id == 'GBP'){
        this.setState({ gbp: e.target.value, eur: (e.target.value * this.state.rate).toFixed(2) })
      }else{ this.setState({ eur: e.target.value, gbp: (e.target.value / this.state.rate).toFixed(2) })}
    }
  }

  render() {

    var revealTooltip = this.state.hideLatest ? {opacity: 0} : {opacity: 1, top: '50%'}; 
    return (
      <div className="App">
        <Tooltip styling={revealTooltip} />
        <div id="App-Content">
          <h1 id="text-heading"><span id="text-heading-smaller">Simple</span>GBP Euro Converter</h1>
          <CurrentRateData date={this.state.date} rate={this.state.rate}/>
          <div id="section-inputs"> 
            <InputAmount label="GBP" icon="icon fas fa-pound-sign" symbol="£" value={this.state.gbp} change={this.handleEvent} />
            <InputAmount label="EUR" icon="icon fas fa-euro-sign" symbol="€" value={this.state.eur} change={this.handleEvent} />
          </div>
          <div id="section-bottom">
            <AdditionalInfo riseFallPercentage={this.state.hist_perc} hist_rate={this.state.hist_rate} latest={this.state.latest}/>
            <GetRateButton update={this.getRate} />
            <ShareBar />
          </div>
          <span id="text-credit">&copy; 2019 David Andrews</span>
        </div>
      </div>
    );
  }
}

export default App;
