import React, { Component } from 'react';

class CurrentRateData extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        return (
          	<div id="section-top">
            	<span>Exchange rate: <span className="text-bold">{this.props.rate}</span></span>
            	<span id="text-lastupdate">Last update: <span className="text-bold">{this.props.date}</span></span>
        	</div>
        );
    }
}

export default CurrentRateData;
